body {
    font-family: 'Montserrat', sans-serif;
    color: #404040;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    text-align: left;
}

a {
    text-decoration: none;
    transition: 0.35s all cubic-bezier(0.55, 0.085, 0, 0.99);
    cursor: pointer;
}

a:visited,
a:active {
    text-decoration: none;
    transition: 0.35s all cubic-bezier(0.55, 0.085, 0, 0.99);
    cursor: pointer;
}

a:hover {
    text-decoration: none;
}

.whiteLink {
    color: #ffffff;
}

h1 {
    font-weight: 600;
    font-size: 80px;
    line-height: 80px;
    margin: 6px 0;
    color: #ffffff;
}

h2 {
    font-size: 48px;
    line-height: 58px;
    margin: 6px 0;
    color: #000000;
    font-weight: 600;
    max-width: 615px;
}

h5:before {
    content: " ";
    background: #1BBDBF;
    display: block;
    width: 82px;
    height: 7px;
    margin-bottom: 29.5px;
    margin-top: 50px;
}

h3 {
    font-size: 18px;
    line-height: 22px;
    margin: 15px 0 12px 0;
    font-weight: 600;
    color: #128081;
    max-width: 615px;
}

h4 {
    color: #ffffff;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    margin: 8px 0;
    margin-bottom: 16px;
}

p {
    margin: 8px 0;
    width: 55%;
}

img {
    width: 100%;
    height: auto;
}

ol,
ul {
    padding-inline-start: 15px;
}

ol ul,
ul ol,
ul ul,
ol ol {
    padding-inline-start: 25px;
}

ol li ul li,
ol li,
ul li {
    margin-top: 16px;
}

.button {
    display: inline-block;
    border-radius: 25px;
    background: #B2292E;
    color: #ffffff;
    padding: 7px 30px;
    transition: 0.35s all cubic-bezier(0.55, 0.085, 0, 0.99);
}

.button:hover {
    background: #86191d;
}

.license-button {
    margin-top: 54px;
}

header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    height: 80px;
    background: #262626 0% 0% no-repeat padding-box;
    padding-left: 7%;
    padding-right: 7%;
    color: #ffffff;
}

header a {
    color: #ffffff;
    margin-left: auto;
    font-weight: 700;
}

header a:hover {
    color: #e0e0e0;
}

header .logos {
    max-width: 209px;
    margin-right: 20px;
}

#hero-section {
    display: block;
    padding-left: 7%;
    padding-right: 7%;
}

#hero-section span.contactInfo {
    display: block;
    line-height: 19px;
    color: #000000;
    padding-top: 29px;
}

#hero-section span.contactInfo a {
    color: #000000;
}

#licensing,
#brands,
#LicenseNow,
#licensingNote {
    margin-left: 7%;
    margin-right: 7%;
}

#brands {
    margin-top: 100px;
}

#licensing .phone {
    max-width: 45%;
    display: inline-block;
    float: right;
}

.brand-display {
    margin-top: 38px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between;
}

.brand-display div {
    max-width: 46%;
    flex: 1 1 50%;
    padding-bottom: 60px;
}

.brand-display div img {
    width: 100%;
    box-shadow: 0 20px 36px 0 rgba(0, 0, 0, 0.1);
}

