label,
input,
textarea {
    display: block;
}

textarea {
    resize: none;
    height: 283px;
}

textarea,
input {
    border-radius: 4px;
    border: #DDDDDD 1px solid;
    padding: 20px 30px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
    color: #404040;
    font-size: 16px;
    line-height: 22px;
    width: calc(100% - 110px);
}

label {
    font-weight: 700;
    margin-bottom: 18px;
    margin-top: 25px;
}

button {
    margin-top: 30px;
    outline: none;
    box-shadow: none;
    border: 0;
    cursor: pointer;
}

footer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    background: #262626 0% 0% no-repeat padding-box;
    padding-left: 7%;
    padding-right: 7%;
    padding-top: 80px;
    padding-bottom: 40px;
    color: #ffffff;
    margin-top: 100px;
}

footer p {
    font-weight: 500;
    line-height: 24px;
    /* margin: 0; */
    width: 100%;
}

footer #col2 {
    margin-left: 50px;
}

@media (max-width: 1079px) {
    #hero-section h1 {
        display: block;
        margin-top: calc(10vh - 40px);
    }
}

@media (max-width: 925px) {
    body {
        font-size: 14px;
        line-height: 20px;
    }

    h1 {
        font-size: 60px;
        line-height: 60px;
    }

    h2 {
        font-size: 38px;
        line-height: 48px;
        max-width: unset;
    }

    h3 {
        font-size: 17px;
        line-height: 21px;
        max-width: unset;
    }

    h4 {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 16px;
    }

    header {
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: space-around;
        height: 160px;
        padding-left: 7%;
        padding-right: 7%;
        color: #ffffff;
    }

    header a {
        margin-left: unset;
    }

    #hero-section {
        margin-bottom: 180px;
    }

    textarea,
    input {
        padding: 15px 20px;
        font-size: 14px;
        line-height: 20px;
        width: 90%;
    }

    label {
        margin-bottom: 13px;
        margin-top: 20px;
    }

    #licensing .phone {
        display: none;
    }

    .brand-display div {
        max-width: 45%;
        flex: 1 1 45%;
        padding-bottom: 40px;
    }

    #LicenseNow {
        flex-direction: column;
    }
}

@media (max-width: 650px) {
    #hero-section:after {
        height: 150px;
        top: calc(100vh - 155px);
    }

    .brand-display div {
        max-width: 90%;
        flex: 1 1 90%;
        padding-bottom: 40px;
    }

    #hero-section {
        margin-bottom: 120px;
    }
}

